
import { yValueFormatter } from '~/libs/netZeroUtils'

export default {
  props: {
    title: {
      type: String,
      required: true
    },
    /**
     * 0 ~ 100
     */
    value: {
      type: Number,
      default: -1
    },
    color: {
      type: String,
      default: '#000'
    },
    type: {
      type: String,
      default: 'fact',
      validator (val) {
        return ['bau', 'fact', 'noLabel', 'roadmap'].includes(val)
      }
    }
  },
  computed: {
    valueDiff () {
      return this.value - 100
    },
    valueLabel () {
      if (this.valueDiff !== 0) {
        return yValueFormatter(Math.abs(this.valueDiff))
      }
      return ' 0'
    },
    valueClass () {
      if (this.valueDiff > 0) {
        return 'esgLegend__value--raise'
      } else if (this.valueDiff < 0) {
        return 'esgLegend__value--reduce'
      }
      return ''
    }
  }
}
